import React from "react"

import styles from "./workshop.module.scss"

import Layout from "../components/layout"
import { TimelineLite } from "gsap/all"

class WorkShopPage extends React.Component {
  constructor(props) {
    super(props)

    // logo container
    this.container = null
    // objects
    this.tween = null

    this.state = {
      color: "#FFF",
    }
  }

  componentDidMount() {
    if (window.innerWidth <= mobileDeviceWidth) {
      const viewerImage = document.getElementById("myElement")
      const hammertime = new Hammer(viewerImage)
      this.tween = new TimelineLite({ paused: true }).to(this.container, 2, {
        y: 100,
      })
      hammertime.get("swipe").set({ direction: Hammer.DIRECTION_VERTICAL })
      hammertime.on("swipedown", e => {
        console.log("HAMMERTIME: TAP")
        this.tween.play()
        viewerImage.textContent = e.type + " gesture detected"
      })
      hammertime.on("swipeup", e => {
        viewerImage.textContent = e.type + " gesture detected"
        this.tween.reverse()
      })
    }
  }

  componentWillUnmount() {}

  render() {
    return (
      <Layout>
        <div
          ref={div => (this.container = div)}
          id="myElement"
          className={styles.card}
        >
          {this.props.children}workshop
        </div>
      </Layout>
    )
  }
}
export default WorkShopPage
